body {
    margin: 0;
    /*font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',*/
    /*'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',*/
    /*sans-serif;*/
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    list-style: none;
    text-decoration: none;
}

*:hover {
    text-decoration: none;
}
a:hover {
    text-decoration: none;
}
input:focus {
    outline: none;
}
.wrap {

}
.container {
    padding: 0!important;
}
.title-bar {
    background: #2196F3;
}
.title-bar p {
    margin: 0;
    padding: 15px;
    color: #fff;
}
.form {
    padding-bottom: 30px;
    margin: 25px 10px;
    border-bottom: 1px solid #ddd;
}
.form-content {
    width: 90%;
    margin: 25px auto;
}
.form input {
    margin-top: 15px;
    border: 0;
    border-bottom: 1px solid #2196F3;
}
.form input:focus {
    border-bottom: 2px solid #2196F3;
}
.form input, .form button {
    display: block;
    margin: 20px auto;
    padding: 8px 15px;
    width: 100%;
}
.phone {
    display: flex;
    margin: 15px 0;
}
.phone select {
    width: 66px;
    background: #fff;
    border: 0;
}
.phone select:focus {
    background: #fff;
}
.btn-com {
    margin-top: 20px !important;
    background-color: #2196F3;
    color: #fff;
    border: 0;
    border-radius: 3px;
}
.btn-com a {
    display: block;
    color: #fff;
}
.icon {
    padding: 0 10px 0 5px;
}
.cl-white {
    color: #fff;
}
.custom-control-label {
    padding-top: 4px;
}
.box .text {
    padding-left: 1.5rem;
}
